	module.exports = {"data":{"markdownRemark":{"html":"<p>In this episode, I sit down with Svein-Tore, the founder of H5P, an amazing open source authoring tool for creating elearning interactions. We discuss the story behind H5P, the benefits of using an open-source tools and the opportunities that H5P brings newbie IDs.</p>\n<iframe src=\"https://simplecast.com/e/74665?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>This is an amazing set of tools bundled together FOR FREE! It’s constantly being worked on and updated and added to. If you’re an ID newbie looking to create interesting things, including interactive video, give H5P a try! Just a heads up to all you fabulous listeners: Dear ID will be on hiatus until I can record some more episodes. I’m looking for IDs willing to share their journey. If that’s you, please get in touch! Also, as always, <a href=\"itms://itunes.apple.com/us/podcast/dear-instructional-designer/id1111995456?mt=2\">leaving a rating and review for us on iTunes </a>would be much appreciated!</p>","frontmatter":{"date":"June 29, 2017","path":"/blog/dear-id-episode-47-svein-tore-on-h5p","title":"#Dear ID Episode 47: Svein-Tore on H5P","tags":["authoring tools","elearning","free","interactive video","open source"],"categories":["Dear ID Show","elearning"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":3,"src":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg","srcSet":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c7d05.jpg 203w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-784f5.jpg 405w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg 810w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-adada.jpg 1215w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-1c372.jpg 1500w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

